@font-face {
    font-family: "NanumSquare";
    font-style: normal;
    font-weight: 100;
    src: url("../../../styles/fonts/NanumSquareB.eot") format("eot"), url("../../../styles/fonts/NanumSquareB.woff") format("woff"), url("../../../styles/fonts/NanumSquareB.ttf") format("truetype");
}

@font-face {
    font-family: "NanumSquare";
    font-style: normal;
    font-weight: normal;
    src: url("../../../styles/fonts/NanumSquareR.eot") format("eot"), url("../../../styles/fonts/NanumSquareR.woff") format("woff"), url("../../../styles/fonts/NanumSquareR.ttf") format("truetype");
}
.Loginmargin {
    margin-top: 70px;
}

.Loginlogo {
    width: 533px;
    height: 200px;
}

.helpbox {
    width: 100%;
}
.links {
    float: right;
    font-family: "Noto Sans CJK KR";
    font-size: 20px;
    padding-left: 20px;
    font-weight: bold;
    margin-top: 10px;
}
.plainbox {
    width: 100%;
    min-width: 430px;
    margin-top: 30px;
    text-align: center;
}
.plain {
    font-family: "Noto Sans CJK KR";
    font-size: 22px;
    font-weight: bold;
}
.regibtn {
    width: 500px;
    height: 60px;
    border-radius: 10px;
    font-family: "Noto Sans CJK KR";
    background-color: rgba(70, 225, 106, 0.69);
    border-color: rgba(3, 137, 34);
    font-size: 20px;
    font-weight: bold;
    padding: 0 0 0 0;
    margin-top: 30px;
    margin-bottom: 10px;
}
.regibtn:hover {
    -webkit-transition: all 0.2s ease-in-out;
    -moz-transition: all 0.2s ease-in-out;
    -o-transition: all 0.2s ease-in-out;
    transition: all 0.2s ease-in-out;

    background-color: rgba(52, 189, 84, 0.75);
}
.links a:link {
    color: gray;
    text-decoration: none;
}
.links a:visited {
    color: gray;
    text-decoration: none;
}
.links a:hover {
    color: gray;
    text-decoration: none;
}
.errmsg {
    color: red;
    font-size: 13px;
    margin-top: 10px;
    margin-left: 15px;
}
.AdminLoginMid {
    width: 200px;
    height: 50px;
    margin: 0 auto;
    text-align: center;
}
.AdminLoginMid {
    font-family: "Noto Sans CJK KR";
    font-size: 20px;
    font-weight: bold;
}
