._carditem a:link {
    color: black;
    text-decoration: none;
}
._carditem a:visited {
    color: black;
    text-decoration: none;
}
._carditem a:hover {
    color: black;
    text-decoration: none;
}
.wrap {
    width: 1280px;
    margin: 0 auto;
}

._cardlist {
    text-align: center;
    overflow: hidden;
    width: 1280px;
    padding-top: 10px;
    margin: 0 auto;
    margin-top: 50px;
    padding-bottom: 50px;
}
._carditem {
    width: 250px;
    height: 180px;
    border-radius: 30px;
    display: inline-block;
    vertical-align: middle;
    margin-left: 20px;
    margin-bottom: 5px;
    box-shadow: 0 1px 5px rgba(0, 0, 0, 0.3);
}

._carditem img {
    margin-top: 15px;
    width: 130px;
    height: 130px;
}

._carditem article {
    font-family: "Noto Sans CJK KR";
    padding: 2px;
    display: flex;
    flex: 1;
    margin-top: 5px;
    justify-content: space-between;
    flex-direction: column;
}

._carditem h1 {
    font-size: 15px;
}
._carditem:hover {
    transform: scale(0.9, 0.9);
    box-shadow: 5px 5px 30px 15px rgba(0, 0, 0, 0.25),
        -5px -5px 30px 15px rgba(0, 0, 0, 0.22);
}
